<template>
    <div :class="['mole', alive ? 'alive' : 'dead']">
        <transition
            name="mole-move"
            @after-leave="$emit('after-leave', mole.hole)"
        >
            <span v-if="mole.show && mole.alive">üêπ</span>
        </transition>
    </div>
</template>

<script>
export default {
    props: {
        mole: {
            type: Object,
            default: () => ({}),
        },
    },
    computed: {
        alive() {
            return this.mole.alive !== undefined ? this.mole.alive : null;
        }
    },
    watch: {
        alive(newState) {
            if (newState === false) {
                this.kill();
            }
        }
    },
    methods: {
        kill() {
            const donk = new Audio(require('../assets/bonk.mp3'));
            donk.play();
            this.$emit('killed');
        }
    }
}
</script>

